$results-tree-head-height-pc: 40px;
$results-tree-team-height-pc: 48px;

$results-tree-table-line-height-pc: 1px;
$results-tree-table-row-height-pc: calc(#{$results-tree-head-height-pc} - #{$results-tree-table-line-height-pc});

$results-tree-bottom-offset-pc: calc(#{$results-tree-head-height-pc} - #{$results-tree-team-height-pc} / 2);
$results-tree-bottom-double-offset-pc: calc((#{$results-tree-head-height-pc} - #{$results-tree-team-height-pc} / 2) * 2);

$results-tree-item-bottom-pc: calc((#{$results-tree-head-height-pc} - #{$results-tree-team-height-pc} / 2) * 2 + #{$results-tree-head-height-pc});

$results-tree-score-item-size-pc: 22px;
$results-tree-score-bg: rgba(255,255,255,0.8);

$results-tree-arrow-size-pc: 10px;

.results-tree {
	background-color: $gray-superlight;
	padding-top: $results-tree-bottom-offset-pc;
	position: relative;

	&__container {

		&:before {
			content: "";
			border-right: 1px dotted $gray-169;
			position: absolute;
			top: $results-tree-head-height-pc;
			left: 50%;
			height: calc(100% - #{$results-tree-head-height-pc});
			width: 1px;
		}
	}

	&__table {
		background-image: repeating-linear-gradient(transparent 0px, transparent $results-tree-table-row-height-pc, $gray-225 $results-tree-table-row-height-pc, $gray-225 $results-tree-head-height-pc);
		display: flex;
		padding-bottom: 50px;
	}

	&__col {
		width: 50%;

		&_right {

			.results-tree__score {
		
				&:hover {
		
					&:before {
						border-left-color: white;
						border-right-color: $gray-187;
					}
				}
			}

			.results-tree__score-table {
				left: auto;
				right: calc(100% - 1px);
			}
		}
	}

	&__block {
		text-align: center;
	}

	&__head {
		font-size: 19px;
		margin-bottom: $results-tree-bottom-offset-pc;
		padding-top: 9px;
		position: relative;
		height: $results-tree-head-height-pc;

		&:after {
			content: "";
			background-image: linear-gradient(to bottom, rgba(0,0,0,0.1), rgba(0,0,0,0));
			position: absolute;
			top: 100%;
			left: 0;
			height: 5px;
			width: 100%;
		}
	}

	&__items {
		padding-bottom: $results-tree-item-bottom-pc;

		&:last-child {
			padding-bottom: $results-tree-bottom-offset-pc;
		}
	}

	&__items-wrap {
		display: inline-block;
		position: relative;
	}

	&__items-container {
		display: inline-block;
		position: relative;
		width: 192px;
	}
	
	&__score {
		transition: $trans;
		background-color: transparent;
		border: 1px solid transparent;
		transform: translate(0, -50%);
		padding: 4px;
		position: absolute;
		top: 50%;
		left: calc(100% + 22px);
		z-index: 3;

		&:before {
			content: "";
			transition: $trans;
			background-color: transparent;
			border: 1px solid transparent;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			z-index: -1;
		}

		&:hover {

			&:before {
				background-color: $results-tree-score-bg;
				border-color: $gray-187;
				border-right-color: white;
			}

			.results-tree__score-table {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__score-container {
		background-color: $gray-superlight;
		border: 1px solid $gray-169;
		padding: 2px;
	}

	&__score-item {
		background-color: $pink;
		color: white;
		display: flex;
		align-items: center;
		justify-content: center;
		text-shadow: 0 1px 1px rgba(0,0,0,1);
		height: $results-tree-score-item-size-pc;
		width: $results-tree-score-item-size-pc;

		&_top {
			background-color: $green;
		}
	}

	&__score-separator {
		background-color: $gray-light;
		display: block;
		margin: 2px;
		height: 1px;
	}

	&__score-table {
		transition: $trans;
		background-color: $results-tree-score-bg;
		border: 1px solid $gray-187;
		font-size: 14px;
		opacity: 0;
		visibility: hidden;
		padding: 2px 5px;
		position: absolute;
		left: calc(100% - 1px);
		top: 0;
		z-index: -2;
	}

	&__score-row {
		display: flex;

		&:last-child {

			.results-tree__score-col {
				border-bottom: 0;
			}
		}
	}

	&__score-col {
		border-bottom: 1px solid $gray-lighten;
		padding: 5px 10px 3px;

		&:first-child {
			padding-left: 5px;
		}
		
		&:nth-child(2) {
			font-size: 13px;
			padding-left: 20px;
			padding-right: 30px;
		}

		&:last-child {
			text-align: right;
		}
	}

	&__item {
		position: relative;
		
		&:first-child {
			margin-bottom: $results-tree-bottom-double-offset-pc;
		}

		&:before {
			content: "";
			background-color: currentColor;
			position: absolute;
			left: calc(100% + 40px);
			height: 20px;
			width: 2px;
		}

		&:first-child {

			&:before {
				top: 50%;
			}
		}

		&:last-child {

			&:before {
				bottom: 50%;
			}
		}

		&:not([data-target]) {

			&:after {
				content: "";
				transform: translateY(-50%);
				background-color: currentColor;
				position: absolute;
				top: 50%;
				left: 100%;
				height: 2px;
				width: 42px;
			}
		}
	}

	&__team {
		background-color: $gray-superlight;
		border: 1px solid $gray-169;
		display: flex;
		align-items: center;
		padding: 5px 10px;
		position: relative;
		height: $results-tree-team-height-pc;
		z-index: 2;
	}

	&__team-img {
		display: flex;
		align-items: center;
		flex-shrink: 0;
		height: 100%;
		width: 50px;
	}

	&__team-icon {
		display: block;
	}

	&__team-name {
		font-size: 15px;
		line-height: 1.1;
		flex-shrink: 1;
		flex-grow: 1;

		a {
			display: block;
		}
	}

	&__connector {
		position: absolute;
	}

	&__connector-line {
		background-color: currentColor;
		position: absolute;
	}

	&__arrow {
		background-color: $blue;
		top: 50%;
		position: absolute;
		height: 56px;
		width: 33px;

		&:hover {
			background-color: $blue-dark;
		}

		&:before {
			content: "";
			transform: translate(-50%, -50%) rotate(45deg);
			position: absolute;
			top: 50%;
			left: 50%;
			border-left: 1px solid white;
			border-bottom: 1px solid white;
			height: $results-tree-arrow-size-pc;
			width: $results-tree-arrow-size-pc;
		}
		
		&_left {
			left: 0;
		}

		&_right {
			transform: scaleX(-1);
			right: 0;
		}
	}
}

.jqSimpleConnect {
	z-index: 1;
}