.footer {
	
	&__top {
		background-color: $gray-superdark;
	}

	&__container {
		padding: 26px $el-side-padding-pc 16px;
		
		&_top {
			display: flex;
		}

		&_bottom {
			color: white;
			display: flex;
			justify-content: space-between;
		}

		&_bottom-black {
			display: flex;
			justify-content: space-between;
			padding-top: 12px;
			padding-bottom: 10px;
		}
	}

	&__top-item {

		&:not(:last-child) {
			margin-right: 60px;
		}
	}

	&__title {
		font-size: 13px;
		text-transform: uppercase;
		color: white;
		margin-bottom: 10px;
	}

	&__top-block {
		display: flex;
		justify-content: space-between;
		margin-bottom: 1em;
	}

	&__top-list {
		font-size: 12px;
		line-height: 1.9;

		&:not(:last-child) {
			margin-right: 24px;
		}
	}

	&__top-list-link {
		color: $gray;
	}

	&__bottom {
		background-color: $gray-dark;
		font-size: 15px;
		line-height: 2;
	}

	&__bottom-item {
		
		&_menu {
			display: flex;
		}

		&_contacts {
			display: flex;
			flex-flow: column;
			align-items: flex-end;
			text-align: right;
		}
	}

	&__bottom-list {
		margin-right: 80px;
	}

	&__bottom-black {
		background-color: black;
		color: $gray;

		div {
			
			span {
				line-height: 0.2;
				position: relative;
				top: -2px;
			}
		}

		a {
			color: $gray;

			&:hover {
				color: white;
			}
		}
	}

	&__copy {
		color: $gray-light;
		line-height: 1.2;
		width: 100%;

		a {
			color: $gray-light;

			&:hover {
				color: white;
			}
		}
	}
}