.select {
	
	.jq-selectbox {

		&.focused {

			.jq-selectbox__select {
				border-color: $gray;
			}
		}

		&__select {
			background-color: white;
			background-image: none;
			border-radius: 0;
			border: 1px solid $gray;
			box-shadow: none;
			font-family: "Circe", Arial, Helvetica, sans-serif;
			font-size: 12px;
			padding-right: 26px;
			height: 32px;
			width: 136px;
		}

		&__trigger {
			border: none;
		}

		&__dropdown {
			border: none;
			border-radius: 0;
			box-shadow: none;
			font-family: "Circe", Arial, Helvetica, sans-serif;
			font-size: 12px;
			margin: 0;
		}

		&__trigger-arrow {
			transform: translate(-10%, -70%) rotate(45deg);
			border: none;
			border-right: 2px solid $gray-darken;
			border-bottom: 2px solid $gray-darken;
			top: 50%;
			left: 50%;
			right: auto;
			height: 10px;
			width: 10px;
		}


		li {
			font-weight: 300;
			line-height: 1;

			&.selected {
				background-color: $gray-superlight;
				color: black;
			}

			&:hover {
				background-color: $gray-superlight;
				color: black;
			}

			&:first-child {
				padding-top: 10px;
			}
		}
	}
}