.team-history {
	display: flex;
	flex-flow: column;
	position: absolute;
	width: 100%;
	height: 100%;

	&__collapse-block {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;

		.container {
			padding-left: $el-side-padding-pc;
		}
	}

	&__head {
		flex-shrink: 0;
		background-color: $gray;
		color: white;
		padding: 6px $el-side-padding-pc 3px;
	}

	&__table-container {
		flex-grow: 1;
		flex-shrink: 1;
		position: relative;

		&:before {
			content: ".";
			display: block;
		}
	}

	&__table {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
	}
	
	&__collapse-block-head {
		font-size: 16px;
		font-weight: normal;
		text-transform: none;
	}
}