.online-table {
	
	&__grid {
		width: 100%;
	}

	&__grid-row {
		border-top: 1px solid white;

		&:first-child {
			border-top: none;

			.online-table__grid-col {
				padding-top: 2px;
				padding-bottom: 2px;

				&:last-child {
					padding-right: 0;
				}
			}
		}

		&_decor {

			&:not(:first-child) {

				&:nth-child(odd) {
					background-color: white;
				}

				.online-table__grid-col {
					
					&:not(:first-child) {
				
						&:nth-child(even) {
							background-color: rgba(0,0,0,0.1);
						}
					}

					&:last-child {
						padding-right: 0;
					}
				}
			}
		}
	}

	&__grid-col {
		text-align: center;
		padding-top: 4px;
		padding-bottom: 5px;

		&:first-child {
			text-align: center;
			padding-left: 30px;
			padding-right: 30px;
			white-space: nowrap;
			width: 1%;

			.online-table__result {
				margin: 0 25px;
			}
		}

		&:last-child {
			padding-right: 13%;
		}
	}

	&__result {
		vertical-align: middle;
		text-align: center;
	}

	&__result-number {
		font-size: 14px;

		&:first-child {
			padding-right: 6px;
		}

		&:last-child {
			padding-left: 6px;
		}
	}

	&__icon {
		display: inline-block;
		vertical-align: middle;
	}
}