$collapse-block-arrow-size-pc: 18px;
$collapse-block-arrow-border-pc: 4px;

.collapse-block {

	&_open {

		.collapse-block__head-container {

			&:before {
				transform: translateY(-70%) rotate(-135deg);
			}
		}
	}

	&__head {
		background-color: $gray;
		display: block;
		color: white;
		font-size: 15px;
		font-weight: bold;
		text-transform: uppercase;
		text-decoration: none;
		position: relative;
	}

	&__head-container {
		display: block;
		padding: 6px 0 3px; // 6px $el-side-padding-pc 3px;

		// &:before {
		// 	content: "";
		// 	transition: $trans;
		// 	transform: translateY(-30%) rotate(45deg);
		// 	border-top: $collapse-block-arrow-border-pc solid white;
		// 	border-left: $collapse-block-arrow-border-pc solid white;
		// 	position: absolute;
		// 	top: 50%;
		// 	right: calc(#{$side-padding-pc} + 24px);
		// 	height: $collapse-block-arrow-size-pc;
		// 	width: $collapse-block-arrow-size-pc;
		// }
	}
	
	&__title {
		display: block;
	}

	&__body {
		transition: $trans;
		// height: 0;
		// overflow: hidden;
	}

	&__content {
		position: relative;
	}
}