.teams-win {
	background-color: $gray-superlight;
	
	&__items {
		display: flex;
		align-items: center;
		padding: 6px 0;
	}

	&__item {
		flex-basis: calc(100% / 3);
		font-size: 14px;
		font-weight: bold;
		text-align: center;
		text-transform: uppercase;

		&:not(:nth-child(2)) {
			display: flex;
			align-items: center;
			justify-content: flex-end;
		}

		&:last-child {
			justify-content: flex-start;
		}
	}

	&__img {
		max-height: 36px;
	}

	&__score {
		font-size: 19px;
		font-weight: bold;
		padding: 0 14px;
	}
}

