.calendar-widget {
	width: 134px;

	&__head {
		margin-bottom: 12px;
	}

	&__month {
		font-size: 16px;
		font-weight: bold;
	}

	&__body {
		text-align: right;
		padding-left: 3px;
	}
	
	&__weekdays {
		border-bottom: 1px solid $gray;
		color: $gray;
		display: flex;
		font-size: 12px;
	}

	&__days {
		display: flex;
		flex-flow: wrap;
		font-size: 11px;
		line-height: 1.7;
	}

	&__day {
		padding-right: 3px;
		width: calc(100% / 7);

		&_active {
			background-color: $blue;
			color: white;
		}

		&_other {
			color: $gray;
		}
	}
}