$links-list-icon-size: 32px;

.links-list {
	background-color: $gray-lightest;
	height: 100%;

	&__head {
		background-color: gray;
		color: white;
		text-transform: uppercase;
		font-weight: 300;
		font-size: 14px;
		text-align: center;
		padding: 8px 30px 6px;
	}

	&__table {
		font-size: 15px;
		padding: 12px 30px 20px;
	}

	&__table-item {
		display: flex;
		align-items: center;
		color: $blue;
		text-decoration: none;
		margin-bottom: 2px;

		&_big-offset {
			margin-bottom: calc(#{$links-list-icon-size} + 2px);
		}

		&:hover {
			color: $gray;
		}
	}
	
	&__table-icon {
		border: 1px solid $gray-187;
		display: block;
		margin-right: 25px;
		height: $links-list-icon-size;
		width: $links-list-icon-size;
	}

	&__table-text {
		display: block;
	}
}