$checkbox-size-pc: 11px;
$checkbox-check-size-pc: 5px;

.checkbox {
	margin-bottom: 1px;
	
	&__input {
		display: none;

		&:checked {

			& + .checkbox__block {

				.checkbox__action {

					&:before {
						border-color: $red-light;
					}
				}
			}
		}
	}

	&__block {
		display: block;
		padding-left: 15px;
		position: relative;
		min-height: 16px;
		min-width: $checkbox-size-pc;
	}

	&__action {
		background-color: white;
		border: 1px solid $gray;
		display: inline-block;
		margin-right: 5px;
		position: absolute;
		top: 6px;
		left: 0;
		height: $checkbox-size-pc;
		width: $checkbox-size-pc;

		&:before {
			content: "";
			transform: translate(-50%, -70%) rotate(35deg);
			border-right: 1px solid transparent;
			border-bottom: 1px solid transparent;
			position: absolute;
			top: 50%;
			left: 50%;
			height: 7px;
			width: 4px;
		}
	}

	&__title {
		font-size: 13px;
		font-weight: 300;
	}
}