$results-table-gray-opacity: $gray-opacity-6;

.results-table {
	background-color: $gray-lightest;
	position: relative;

	&__head-bg {
		position: absolute;
		left: 0;
		width: 100%;
	}
	
	&__head {
		background-color: $gray;
		color: white;
		font-weight: bold;
		font-size: 15px;
		text-transform: uppercase;
		text-align: center;
		padding: 4px 10px;
	}

	&__collapse-block {
		background-color: $gray-superlight;
	}

	&__collapse-block-head {
		font-weight: normal;
		text-transform: none;
		font-size: 16px;
	}

	&__grid {
		width: 100%;

		&_score {
			
			.results-table__row {

				&:nth-child(4n + 5),
				&:nth-child(4n + 6) {
					background-color: white;
				}

				&:nth-child(4n + 3) {
					
					.results-table__col:not(:nth-child(2)) {
						border-bottom: 1px solid white;
					}
				}

				&:nth-child(4n + 5) {

					.results-table__col:not(:nth-child(2)) {
						border-bottom: 1px solid $gray-light;
					}
				}

				&:nth-child(4n + 3),
				&:nth-child(4n + 5) {

					.results-table__col {
						
						&:nth-child(2) {
							font-size: 19px;
							padding-top: 8px;
							padding-left: 13px;
							padding-right: 13px;
						}
					}
				}

				&:last-child {

					.results-table__col {
						border-bottom: none;
					}
				}

				.results-table__col:not(.results-table__col_head) {
					border-bottom: 1px solid transparent;

					&:first-child {
						background-color: $results-table-gray-opacity;
						text-align: right;
						padding-right: 15px;
					}

					&:last-child {
						background-color: $results-table-gray-opacity;
					}

					&:nth-last-child(3) {
						background-color: $results-table-gray-opacity;
					}
				}
			}
		}

		&_team {

			.results-table__row {
				
				&:nth-child(2n + 4) {
					background-color: white;
				}
			}

			.results-table__col:not(.results-table__col_head) {
				border-bottom: 1px solid transparent;

				&:first-child {
					text-align: left;
					padding-left: 18px;
				}

				&:nth-child(2) {
					background-color: $results-table-gray-opacity;
					border-bottom: 1px solid transparent;
				}

				&:nth-last-child(1),
				&:nth-last-child(2),
				&:nth-last-child(3) {
					background-color: $results-table-gray-opacity;
				}
			}
		}

		&_info {

			thead {

				.results-table__row {
					
					&:first-child {

						.results-table__col {
							padding-top: 20px;

							&:nth-child(2) {
								text-align: left;
							}
						}
					}
				}
			}

			tbody {

				.results-table__row {

					&:first-child {
						
						.results-table__col {
							padding-top: 12px;
						}
					}

					&:last-child {

						.results-table__col {
							padding: 4px 0;
						}
					}
					
					&:nth-child(2n + 2) {
						background-color: white;
					}
				}

				.results-table__col {
					padding-top: 6px;
					padding-bottom: 5px;

					&:first-child {
						text-align: left;
						padding-left: 20px;
					}

					&:nth-child(2) {
						font-size: 15px;
						text-align: left;
						width: 24%;
					}
					
					&:nth-child(8),
					&:nth-child(9),
					&:nth-child(13),
					&:nth-child(14),
					&:nth-child(15),
					&:nth-child(16),
					&:nth-last-child(2),
					&:nth-last-child(3) {
						background-color: $results-table-gray-opacity;
					}

					&:last-child {
						padding-right: 40px;
					}
				}
			}

			tfoot {
				
				.results-table__row {

					.results-table__col {
						background-color: $gray;
						color: white;
						font-weight: 300;
						padding-top: 3px;
						padding-bottom: 2px;

						&:first-child {
							text-align: left;
							padding-left: 20px;
						}
	
						&:nth-child(2) {
							width: auto;
						}
					}
				}
			}
		}

		&_game {

			thead {

				.results-table__col_head {
					padding-top: 4px;
					padding-bottom: 2px;
				}
			}

			tbody {

				.results-table__row {

					&:nth-child(even) {
						background-color: white;
					}
					
					&:first-child {
						
						.results-table__col {
							padding-top: 12px;
						}
					}

					&:last-child {
						
						.results-table__col {
							padding-bottom: 8px;
						}
					}
				}
						

				.results-table__col {

					&:nth-child(3),
					&:nth-child(5),
					&:nth-child(9),
					&:nth-child(10),
					&:nth-child(11),
					&:nth-child(12),
					&:nth-last-child(2),
					&:nth-last-child(3) {
						background-color: $results-table-gray-opacity;
					}
				}
			}

			.results-table__col {

				&:first-child {
					padding-left: 50px;
					text-align: left;
					width: 32%;
				}

				&:last-child {
					padding-right: 40px;
				}
			}
		}

		&_preview {

			.results-table__row {

				&:nth-child(even) {
					background-color: white;
				}

				&:last-child {

					.results-table__col {
						padding: 4px 0;
					}
				}
			}

			.results-table__icon {
				display: block;
				margin: 0 auto;
				max-height: 29px;
			}

			.results-table__col {
				font-size: 15px;
				text-align: left;
				padding-top: 6px;
				padding-bottom: 4px;

				&:first-child {
					width: 14.5%;
				}
				
				&:first-child,
				&:last-child {
					padding-left: 20px;
				}

				&:nth-child(2),
				&:nth-child(4),
				&:nth-child(6) {
					background-color: $results-table-gray-opacity;
				}

				&:nth-child(2),
				&:nth-child(4) {
					width: 9%;
				}

				&:nth-child(3) {
					font-size: 20px;
					font-weight: 300;
					line-height: 1;
					text-align: center;
					width: 15%;
				}

				&:nth-child(5) {
					padding-left: 50px;
				}
			}
		}

		&_dynamic {
			table-layout: fixed;

			.results-table__row {

				&:nth-child(even) {
					background-color: white;
				}
			}

			.results-table__col {
				padding-top: 9px;
				padding-bottom: 6px;

				&_head {
					padding-top: 14px;
					padding-bottom: 10px;
				}

				&:not(.results-table__col_head) {
					font-size: 16px;
				}
				

				&:first-child,
				&:last-child {
					width: 5%;
				}

				&:nth-child(2) {
					text-align: left;
					padding-left: 8px;
				}

				&:nth-last-child(2) {
					text-align: right;
					padding-right: 8px;
				}

				&:nth-child(2),
				&:nth-last-child(2) {
					width: 14%;
				}

				&:nth-child(3),
				&:nth-last-child(3) {
					background-color: $results-table-gray-opacity;
					width: 12%;
				}

				&:nth-child(4),
				&:nth-last-child(4) {
					padding-top: 0;
					padding-bottom: 0;
					vertical-align: middle;
					width: 14%;

					img {
						display: inline-block;
						vertical-align: middle;
					}
				}

				&:nth-child(5) {
					background-color: $results-table-gray-opacity;
				}
			}
		}
	}

	&__col {
		font-size: 14px;
		padding-top: 7px;
		padding-bottom: 5px;
		padding-left: 4px;
		padding-right: 4px;
		position: relative;
		text-align: center;
		vertical-align: middle;
		
		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 3px;
		}
		
		&_head {
			background-color: $gray-superdark;
			color: white;
			font-size: 11px;
			font-weight: 300;
			padding-top: 8px;
			padding-bottom: 4px;
		}

		&_blue {

			&:before {
				background-color: $blue;
			}
		}

		&_red {

			&:before {
				background-color: $red;
			}
		}
	}
}