.favorites-table {
	display: none;
	justify-content: space-between;
	padding: 20px 0 24px;

	&_show {
		display: flex;
	}

	&__item {
		flex-basis: calc(100% / 3);
		// text-align: center;
		// width: calc(100% / 3);

		// &:first-child {
		// 	text-align: left;
		// }

		// &:last-child {
		// 	text-align: right;
		// }
	}

	&__item-container {
		display: inline-block;
	}

	&__name {
		display: inline-block;
		font-size: 17px;
		margin-bottom: 4px;
	}

	&__body {
		display: flex;
	}

	&__photo {
		transition: $trans;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		border: 1px solid transparent;
		cursor: pointer;
		flex-shrink: 0;
		position: relative;
		height: 88px;
		width: 60px;

		&:hover {
			border-color: $gray;

			.team-tooltip {
				opacity: 1;
				visibility: visible;
			}
		}

		.team-tooltip {
			top: 0;
			left: calc(100% + #{$team-tooltip-arrow-size-pc});
		}
	}

	&__info {
		flex-shrink: 1;
		padding-left: 20px;
		padding-top: 44px;
		font-size: 13px;
		line-height: 1.8;
	}

	&__info-item {

		span {
			font-weight: bold;
		}
	}
}