.input {

	&__label {
		display: flex;
		align-items: center;
		font-size: 12px;
		font-weight: 300;
		line-height: 1;
	}
	
	&__title {
		display: block;
		margin-right: 8px;
	}

	&__field {
		background-color: white;
		border: 1px solid $gray;
		box-shadow: none;
		outline: none;
		font-family: "Circe", Arial, Helvetica, sans-serif;
		font-size: 12px;
		padding-right: 10px;
		padding-left: 10px;
		height: 32px;
		width: 136px;
		
		&_xl {
			width: 230px;
		}

		&_l {
			width: 158px;
		}
	}
}