$news-table-head-size: 32px;

.news-table {
	position: relative;
	//height: calc(100% - #{$info-card-mb-pc});

	&_l {
		height: 750px;
	}

	&_full {
		position: absolute;
		height: 100%;
		width: 100%;
	}

	&__wrap {
		background-color: $gray-superlight;
		display: none;
		font-size: 14px;
		line-height: 1.8;
		position: absolute;
		top: $news-table-head-size;
		bottom: 0;
		left: 0;
		overflow: hidden;
		//height: calc(100% - #{$news-table-head-size});
		width: 100%;
		
		&_show {
			display: block;
		}
	}

	&__item {
		display: block;
		color: black;
		text-decoration: none;
		padding: 20px;

		&:first-child {
			padding-top: 32px;
		}

		&:hover {
			color: black;
			background-color: white;

			.news-table__link {
				font-weight: bold;
			}
		}
	}

	&__date {
		margin-right: 10px;
	}

	&__link {
		color: $blue;
		text-decoration: underline;

		&_subtitle {
			display: block;
			text-transform: uppercase;
		}
	}
}