$team-card-size-pc: 146px;

.team-card {
	background-color: $gray-superlight;
	display: flex;

	&__item {
		padding: 20px;
		
		&_img {
			flex-shrink: 0;
		}

		&_text {
			flex-grow: 1;
			flex-shrink: 1;
			font-size: 15px;
			line-height: 1.3;
			padding-left: 10px;
		}
	}

	&__img {
		background-position: center;
		background-size: contain;
		background-repeat: no-repeat;
		height: $team-card-size-pc;
		width: $team-card-size-pc;
	}

	&__title {
		font-size: 21px;
		line-height: 1.8;
		margin-bottom: 0.7em;
	}
}