.table {
	background-color: $gray-lightest;

	&__container {
		
		&_body {

		}
	}

	&__head {
		background-color: $gray-superdark;
		text-transform: uppercase;
		color: white;
		font-size: 11px;
		font-weight: 300;

		.table__col {
			padding-top: 20px;
			padding-bottom: 10px;
		}
	}

	&__body {
		font-size: 12px;
		padding-bottom: 20px;
		position: relative;

		.table__row-wrap {

			&:first-child {

				.table__col {
					padding-top: 13px;
					padding-bottom: 13px;
				}
			}

			&:nth-child(even) {

				.table__row {
					background-color: white;
				}
			}
		}

		.table__col {
			padding-top: 4px;
			padding-bottom: 11px;
		}
	}

	&__row-wrap {
		width: 100%;

		&_active {

			.table__row-container {
				background-color: $blue !important;
				color: white;

				a {
					transition: none;
					color: white;
				}
			}

			.table__row {
				background-color: $blue !important;
			}
		}
	}

	&__row {
		display: flex;
		width: 100%;
	}

	&__col {
		padding-left: 10px;
		padding-right: 10px;

		&:first-child {
			padding-left: $el-side-padding-pc;
		}
	}

	&__collapse {
		transition: $trans;
		height: 0;
		overflow: hidden;
	}

	&_more {

		.table__container_body {
			height: 620px;
		}

		.table__head {
			text-align: center;

			.table__col {

				&:first-child {
					padding-top: 12px;
				}
			}
		}
		
		.table__body {

			.table__row-wrap {

				&:first-child {

					.table__col {

						&:first-child {
							padding-top: 9px;
						}
					}
				}

				&:not(:first-child) {

					.table__col {

						&:first-child {
							padding-top: 0;
						}
					}
				}
			}
		}
	}

	&_more-team {

		.table__body {

			.table__col {

				&:nth-child(3),
				&:nth-child(5),
				&:nth-child(7) {
					background-color: $gray-opacity-6;
				}
			}
		}

		.table__col {

			&:first-child {
				width: 2%;
			}

			&:nth-child(2) {
				width: 25%;
			}

			&:nth-child(3) {
				width: 7%;
				text-align: center;
			}

			&:nth-child(4) {
				width: 23%;
			}

			&:nth-child(5) {
				width: 12%;
				text-align: center;
			}

			&:nth-child(6) {
				width: 10%;
				text-align: center;
			}

			&:nth-child(7) {
				flex-grow: 1;
				text-align: center;
			}
		}
	}

	&_more-player {

		.table__head {
			text-align: left;
		}

		.table__body {

			.table__col {

				&:nth-child(3) {
					background-color: $gray-opacity-6;
				}
			}
		}

		.table__col {

			&:first-child {
				flex-basis: 2%;
			}

			&:nth-child(2) {
				flex-basis: 35%;
			}

			&:nth-child(3) {
				flex-basis: 22%;
			}

			&:nth-child(4) {
				flex-grow: 1;
			}
		}
	}

	&_player-all {
		background-color: transparent;

		.table__head {

			.table__col:not(:first-child) {
				text-align: center;
			}
		}

		a {
			transition: none;
		}

		.table__body {
			font-size: 15px;

			.table__row-wrap {

				&:first-child {

					.table__col {
						padding-top: 10px;
						padding-bottom: 5px;
					}
				}
			}

			.table__row-container {
				background-color: $gray-lightest;

				&:hover {
					background-color: $blue;
					color: white;

					a {
						transition: none;
						color: white;
					}

					.table__row {
						background-color: $blue;
					}
				}
			}

			.table__col {
				padding-top: 8px;
				padding-bottom: 3px;

				&:nth-child(2),
				&:nth-child(4) {
					background-color: $gray-opacity-6;
				}
			}
		}

		.table__col {

			&:first-child {
				flex-basis: 32%;
			}

			&:nth-child(2) {
				flex-basis: 22%;
			}

			&:nth-child(3) {
				text-align: center;
				flex-basis: 21%;
			}

			&:nth-child(4) {
				flex-grow: 25;
			}
		}
	}

	&_team-history {
		position: relative;
		overflow: hidden;
		height: 100%;

		.table__body {
			font-size: 15px;
		}

		.table__row-wrap {

			&:nth-child(even) {
				background-color: white;
			}
		}

		.table__col {

			&:first-child {
				width: 28%;
			}

			&:last-child {
				width: 72%;
			}
		}
	}

	&_teams-more {
		background-color: transparent;

		.table__head {
			background-color: $gray;
		}

		.table__body {
			font-size: 16px;
			padding-bottom: 0;

			.table__row-wrap {

				&:first-child {

					.table__col {
						padding-top: 8px;
						padding-bottom: 7px;
					}
				}
			}

			a {
				transition: none;
			}

			.table__row-container {
				background-color: $gray-lightest;

				&:hover {
					background-color: $blue;
					color: white;

					a {
						color: white;
					}

					.table__row {
						background-color: $blue;
					}
				}
			}

			.table__col {
				padding-top: 5px;
				padding-bottom: 4px;

				&:nth-child(3),
				&:nth-child(5) {
					background-color: $gray-opacity-6;
				}
			}
		}

		.table__col {

			&:first-child {
				width: 5%;
			}

			&:nth-child(2) {
				width: 37%;
			}

			&:nth-child(3) {
				width: 10%;
			}

			&:nth-child(4) {
				width: 18%;
			}

			&:nth-child(5) {
				width: 9%;
			}

			&:nth-child(6) {
				padding-left: 45px;
			}

			&:nth-child(3),
			&:nth-child(4),
			&:nth-child(5) {
				text-align: center;
			}
		}
	}
}