.head-actions {
	background-color: $gray-superlight;

	&_pl-0 {

		.head-actions__container {
			padding-left: 0;
		}
	}
	
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: $el-side-padding-pc;

		> div {
			display: flex;
			align-items: center;
		}
	}
}