$shedule-table-arr: (10.5, 19.5, 20.5);

.shedule-table {
	background-color: $gray-superlight;
	color: white;

	&__grid {

		&_head {
			background-color: $gray-superdark;
			font-size: 11px;
			font-weight: 200;
			text-transform: uppercase;

			.shedule-table__col {
				padding-top: 1.6em;
				padding-bottom: 0.5em;

				&:nth-child(2) {
					padding-left: 15px;
				}

				&:nth-child(3) {
					padding-left: 24px;
				}
			}
		}

		&_body {
			color: black;
			font-size: 15px;

			.shedule-table__row {

				&:first-child,
				&:last-child {

					.shedule-table__col {
						height: 37px;
					}
				}

				&:nth-child(even) {
					background-color: white;
				}
			}

			.shedule-table__col {
				display: flex;
				align-items: center;
				height: 34px;

				&:nth-child(2) {
					padding-left: 15px;
				}

				&:nth-child(3) {
					padding-left: 24px;
				}

				&:nth-child(4) {
					justify-content: center;
				}

				&:nth-child(2),
				&:nth-child(4) {
					background-color: rgba(0,0,0,0.06);
				}
				
				&:not(:nth-child(4)) {
					padding-right: 5px;
				}

				&:last-child {
					padding-right: 30px;
				}
			}
		}
		
		&_head,
		&_body {

			.shedule-table__col {

				@for $i from 0 to length($shedule-table-arr) {
					
					&:nth-child(#{$i + 1}) {
						width: unquote(nth($shedule-table-arr, $i + 1) + '%');
					}
				}
		
				&:nth-child(4) {
					flex-shrink: 0;
					text-align: center;
					min-width: 85px;
					white-space: nowrap;
				}
		
				&:nth-child(5) {
					flex-grow: 1;
					flex-shrink: 1;
				}

				&:not(:last-child) {
			
					div, a {
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}
				}
			}
		}

		&_info {
			background-color: $gray;
			font-size: 15px;
			font-weight: bold;
			text-transform: uppercase;

			.shedule-table__col {
				padding-top: 5px;
				padding-bottom: 3px;
				width: 50%;
			}
		}
	}
	
	&__row {
		display: flex;	
	}

	&__col {

		&:first-child {
			padding-left: $el-side-padding-pc;
		}

		&:last-child {
			text-align: right;
			justify-content: flex-end;
			padding-right: calc(#{$el-side-padding-pc} + 5px);
		}
	}
}