$list-ul-item-size-pc: 4px;

.list {
	line-height: 1.3;
	margin-bottom: 1em;
	
	&__ul {

	}

	&__ul-item {
		padding-left: 30px;
		position: relative;

		&:before {
			content: "";
			background-color: $blue;
			position: absolute;
			left: 0;
			top: calc(0.5em - #{$list-ul-item-size-pc} / 2);
			height: $list-ul-item-size-pc;
			width: $list-ul-item-size-pc;
		}
	}
}