.championship-russia {

	&__note {
		margin-bottom: 24px;
	}
	
	&__item {
		display: none;

		&_show {
			display: block;
		}
	}

	&__table {
		background-color: $gray-superlight;
		display: none;
		padding: $el-side-padding-pc;

		&_show {
			display: block;
		}

		&_playoff {
			padding-top: 5px;
		}
	}
	
	&__table-grid {
		background-color: $beige;
		text-align: center;
		display: flex;
		width: 100%;
	}

	&__table-col {
		transition: $trans;
		flex-grow: 1;
		flex-basis: 30px;
		position: relative;

		&:before {
			transition: $trans;
			content: "";
			position: absolute;
			left: 0;
			bottom: 0;
			height: 3px;
			width: 100%;
		}

		&:not(:first-child) {
			
			&:nth-child(even) {

				&:before {
					background-color: $red;
				}
			}

			&:nth-child(odd) {

				&:before {
					background-color: $blue;
				}
			}
		}

		&:last-child {

			.championship-russia__table-img {
				border-right: 1px solid $gray;
			}
		}

		.team-tooltip {
			left: -7px;
			top: -7px;
		}

		&:hover:not(:first-child) {
			color: white;
			cursor: pointer;

			&:before {
				height: 100%;
			}

			.team-tooltip {
				opacity: 1;
				visibility: visible;
				z-index: 2;
			}
		}
	}
	
	&__table-cell {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 34px;

		span {
			padding-top: 4px;
			position: relative;
			z-index: 1;
		}
	}

	&__table-img {
		border-top: 1px solid $gray;
		border-left: 1px solid $gray;
		border-bottom: 1px solid $gray;
		position: relative;
		z-index: 1;
		width: 100%;
	}
}