.info-card {
	background-color: $gray-superdark;
	margin-bottom: $info-card-mb-pc;

	&_no-side-padding {

		.info-card__body {
			padding-left: 0;
			padding-right: 0;
		}
	}

	&__body {
		padding-left: $el-side-padding-pc;
		padding-right: $el-side-padding-pc;
		position: relative;
	}

	&_gray {
		background-color: $gray-superdark;
	}

	&_gray-light {
		background-color: $gray-darkest;
	}

	&_light-gray {
		background-color: $gray-lightest;
	}
}