$radio-btn-size-pc: 11px;
$radio-btn-check-size-pc: 5px;

.radio-btn {
	
	&__input {
		display: none;

		&:checked {

			& + .radio-btn__block {

				.radio-btn__action {

					&:before {
						background-color: $red-light;
					}
				}
			}
		}
	}

	&__block {
		padding-left: 15px;
		position: relative;
		min-height: $radio-btn-size-pc;
		min-width: $radio-btn-size-pc;
	}

	&__action {
		background-color: white;
		border: 1px solid $gray;
		display: inline-block;
		margin-right: 5px;
		position: absolute;
		top: 6px;
		left: 0;
		height: $radio-btn-size-pc;
		width: $radio-btn-size-pc;

		&:before {
			content: "";
			background-color: transparent;
			position: absolute;
			top: 2px;
			left: 2px;
			height: $radio-btn-check-size-pc;
			width: $radio-btn-check-size-pc;
		}
	}

	&__title {
		font-size: 13px;
		font-weight: 300;
	}
}