$gallery-arrow-container-size-pc: 60px;
$gallery-arrow-size-pc: 18px;
$gallery-modal-close-size-pc: 20px;

.gallery {
	padding-left: $gallery-arrow-container-size-pc;
	padding-right: $gallery-arrow-container-size-pc;
	position: relative;
	
	&__slide {
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		transition: $trans;
		border-radius: 2px;
		border: 2px solid transparent;
		display: block;
		height: 122px;

		&:hover {
			border-color: white;
		}
	}

	&__arrow {
		cursor: pointer;
		position: absolute;
		top: 0;
		height: 100%;
		width: $gallery-arrow-container-size-pc;

		&:before {
			content: "";
			transform: translate(-50%, -50%) rotate(45deg);
			border: 4px solid white;
			position: absolute;
			top: 50%;
			left: 50%;
			height: $gallery-arrow-size-pc;
			width: $gallery-arrow-size-pc;
		}

		&_left {
			left: 0;

			&:before {
				border-top: none;
				border-right: none;
			}
		}

		&_right {
			right: 0;

			&:before {
				border-left: none;
				border-bottom: none;
			}
		}
	}

	&__modal {
		transition: $trans;
		background-color: rgba(0,0,0,0.5);
		opacity: 0;
		visibility: hidden;
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		z-index: 10;

		&_show {
			opacity: 1;
			visibility: visible;
		}
	}

	&__modal-container {
		transform: translate(-50%, -50%);
		background-color: white;
		color: black;
		font-size: 16px;
		padding: 40px 20px 20px;
		position: absolute;
		top: 50%;
		left: 50%;
		width: 680px;
	}

	&__modal-close {
		display: block;
		position: absolute;
		top: 10px;
		right: 20px;
		height: $gallery-modal-close-size-pc;
		width: $gallery-modal-close-size-pc;

		&:hover {

			&:before,
			&:after {
				background-color: black;
			}
		}

		&:before,
		&:after {
			content: "";
			transition: $trans;
			background-color: $gray;
			position: absolute;
			top: 0;
			left: 50%;
			height: 100%;
			width: 3px;
		}

		&:before {
			transform: translateX(-50%) rotate(45deg);
		}

		&:after {
			transform: translateX(-50%) rotate(-45deg);
		}
	}

	&__modal-photo {
		margin-bottom: 15px;
	}

	&__modal-img {
		display: block;
		width: 100%;
	}
}