.fw-bold {
	font-weight: bold;
}

.fw-med {
	font-weight: 500;
}

.fw-reg {
	font-weight: normal;
}

.fw-light {
	font-weight: 300;
}

.ta-center {
	text-align: center;
}

.tt-uppercase {
	text-transform: uppercase;
}

.full-height {
	height: 100%;
}

$colors-arr: (('white', white), ('white', white));

.color {

	@each $item in $colors-arr {
		$name: nth($item, 1);
		$color: nth($item, 2);

		&-#{unquote($name)} {
			color: $color;
		}
	}
}

.link {

	&-h-bold {
		color: $blue;

		&:hover {
			font-weight: bold;
			color: $blue;
		}
	}

	$link-colors: (('black', black, black), ('blue-light', $blue-light, $blue-light), ('gray-dark', $gray-darkest, $gray-darkest));

	@each $item in $link-colors {

		$name: nth($item, 1);
		$color: nth($item, 2);
		$hover: nth($item, 3);
		
		&-#{unquote($name)} {
			color: $color;

			&:hover {
				color: $hover;
			}
		}
	}
}


$number: 50;
$offsets: (('pt', 'padding-top'), ('pb', 'padding-bottom'), ('pr', 'padding-right'), ('pl', 'padding-left'), ('mb', 'margin-bottom'), ('mr', 'margin-right'));

@each $item in $offsets {

	$name: nth($item, 1);
	$value: nth($item, 2);

	@for $i from 1 through $number {
		
		.#{$name}#{$i} {
			#{unquote($value)}: $i + px;
		}
	}
}