.player-info {
	display: flex;
	flex-flow: column;
	justify-content: space-between;
	height: 100%;

	&__collapse-block {

		&:first-child {
			padding-bottom: 26px;
		}
	}

	&__table {
		overflow: auto;
		position: relative;
		height: 200px;
	}

	&__grid {
		background-color: $gray-lightest;
		font-size: 15px;
		width: 100%;
	}

	&__grid-row {

		&:nth-child(odd) {

			.player-info__grid-col {
				padding-top: 12px;
				padding-bottom: 12px;
			}
		}

		&:first-child {

			.player-info__grid-col {
				padding-top: 22px;
				padding-bottom: 11px;
			}
		}

		&:nth-child(even) {
			background-color: white;
		}
	}

	&__grid-col {
		padding: 5px 5px 5px 20px;

		&:not(:first-child) {
			padding-left: 15px;
		}
	}
}