.switch-container {
	font-size: 0;

	&_top-offset {
		padding-top: 4px;
	}

	&_elem-offset {

		.switch-container__btn {
			margin-left: -8px;
		}
	}

	&_align-rigth {
		text-align: right;
	}

	&__btn {
		display: inline-block;
		color: white;
		cursor: pointer;
		font-weight: 200;
		font-size: 12px;
		line-height: 1.2;
		text-transform: uppercase;
		overflow: hidden;
		margin-left: -13px;
		padding: 0 28px 0 26px;
		position: relative;
		
		&:before,
		&:after {
			content: "";
			position: absolute;
			background-color: $gray-darkest;
			height: 300px;
			width: 30px;
			z-index: 1;
		}
	
		&:before {
			transform-origin: 0 0;
			transform: rotate(-18deg);
			top: 0;
			left: 0;
		}
		
		&:after {
			transform: rotate(-18deg);
			transform-origin: 100% 100%;
			bottom: 0;
			right: 0;
		}

		&_blue {
				
			&:before,
			&:after {
				background-color: $blue;
			}
	
			.switch-container__btn-title {
				background-color: $blue;
			}
		}

		&_light-gray {

			&:before,
			&:after {
				background-color: $gray-superlight;
			}
	
			.switch-container__btn-title {
				background-color: $gray-superlight;
			}
		}

		&_gray {
			
			&:before,
			&:after {
				background-color: $gray-darkest;
			}
	
			.switch-container__btn-title {
				background-color: $gray-darkest;
			}
		}
		
		&:first-child {
			margin-left: 0;
			padding: 0 23px 0 21px;
	
			&:before {
				transform: rotate(0);
			}
		}
	
		&_active {

			&.switch-container__btn_blue {
				color: black;

				&:before {
					background-color: $gray-light;
				}
		
				&:after {
					background-color: $gray-light;
				}
		
				.switch-container__btn-title {
					background-color: $gray-light;
				}
				
				// &:first-child {
			
				// 	&:before {
				// 		content: "";
				// 		transform: rotate(0);
				// 		background-color: $gray-light;
				// 	}
			
				// 	&:after {
				// 		background-color: $gray-light;
				// 	}
			
				// 	.switch-container__btn-title {
				// 		background-color: $gray-light;
				// 	}
				// }
			}

			&.switch-container__btn_gray {
				color: black;

				&:before {
					background-color: $gray-superlight;
				}
		
				&:after {
					background-color: $gray-superlight;
				}
		
				.switch-container__btn-title {
					background-color: $gray-superlight;
				}
			}
		}
	}
	
	&__btn-title {
		background-color: $gray-darkest;
		padding: 8px 0 5px 0;
	}

	&__btn-title-text {
		border-bottom: 1px solid currentColor;
		position: relative;
		z-index: 2;
	}
}