.top-note {
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: white;
	font-size: 14px;
	font-weight: 300;
	padding: 6px 20px 5px;

	&_group {
		padding-top: 0;
		padding-bottom: 0;
		padding-right: 0;
	}

	&_blue {
		background-color: $blue;
	}

	&_gray {
		background-color: $gray-darkest;
	}
	
	&__title {
		text-transform: uppercase;
	}
}