.page-widget {
	transition: $trans;
	transform: translateX(-300px);
	background-color: white;
	border: 1px solid black;
	border-left: none;
	position: fixed;
	top: 150px;
	left: 0;
	height: calc(100% - 200px);
	width: 300px;
	z-index: 100;

	&_show {
		transform: translateX(0);
	}

	&__btn {
		background-color: white;
		border: 1px solid black;
		border-left: none;
		cursor: pointer;
		color: $blue;
		font-weight: bold;
		font-size: 40px;
		line-height: 0.4;
		position: absolute;
		left: 100%;
		top: 50px;
		height: 40px;
		width: 40px;

		span {
			transform: translate(-50%, -30%);
			position: absolute;
			top: 50%;
			left: 50%;
		}
	}

	&__table {
		padding: 20px;
		height: 100%;
	}

	&__link {
		color: black;

		&:hover {
			color: $blue-light;
		}
	}
}