$results-note-info-item-img-width-pc: 35%;

.results-note {
	
	&__head {
		background-color: $gray-superdark;
		color: white;
		font-weight: bold;
		font-size: 15px;
		text-transform: uppercase;
		padding: 9px 0;
	}

	&__container {
		
		&_head {
			display: flex;
			justify-content: space-between;
		}
	}

	&__info {
		background-color: $gray-lightest;
		display: flex;
		align-items: center;
		flex-flow: wrap;
		padding: 32px 30px 10px;
		height: 100%;
	}

	&__info-item {
		flex-grow: 1;
		text-transform: uppercase;
		text-align: center;
		padding-bottom: 30px;

		&:nth-last-child(1),
		&:nth-last-child(2),
		&:nth-last-child(3) {
			border-top: 1px solid white;
			padding-top: 8px;
			padding-bottom: 10px;
		}

		&_img {
			flex-basis: $results-note-info-item-img-width-pc;
		}

		&_score {
			flex-basis: calc(100% - #{$results-note-info-item-img-width-pc} * 2);
		}
	}

	&__info-icon {
		display: block;
		margin: 0 auto;
		max-width: 60%;
	}

	&__info-score {
		font-size: 40px;
		font-weight: bold;
	}

	&__table-head {
		background-color: $gray;
		color: white;
		font-weight: 300;
		text-align: right;
		padding: 3px $el-side-padding-pc;
	}

	&__grid {
		background-color: $gray-lightest;
		table-layout: fixed;
		width: 100%;
	}

	&__row {

		&:first-child {

			.results-note__col {
				border-bottom: 1px solid white;
				padding-top: 25px;
			}
		}

		&:nth-child(2n + 3) {
			background-color: white;
		}

		&:last-child {

			.results-note__col {
				padding-bottom: 24px;
			}
		}
	}

	&__col {
		padding: 7px 5px 8px;

		&:first-child {
			padding-left: 20px;
			width: 42%;
		}

		&:nth-child(2n + 2) {
			background-color: rgba(0,0,0,0.08);
		}

		&:nth-child(2n + 3) {
			width: 10%;
		}

		&:last-child {
			padding-left: 10px;
			width: 15%;
		}

		&:not(:first-child):not(:last-child) {
			text-align: center;
		}
	}
}