.grid {
	display: flex;
	margin-right: -($grid-side-padding-pc);
	margin-left: -($grid-side-padding-pc);
	//width: 100%;

	&_sb {
		justify-content: space-between;
	}

	&__col {
		padding-right: $grid-side-padding-pc;
		padding-left: $grid-side-padding-pc;
		position: relative;
		
		@for $i from 1 through 12 {

			&_#{$i} {
				width: calc(100% / (12 / #{$i}));
			}
		}
		
	}
}