.results-team {
	
	&__grid {
		background-color: $gray-lightest;
		table-layout: fixed;
		width: 100%;
	}

	&__row {

		&:first-child {

			.results-team__col {
				border: 1px solid $gray-169;
				

				&:first-child {
					background-color: transparent;
				}
			}
		}

		@for $i from 2 through 30 {

			&:nth-child(#{$i}) {

				.results-team__col {

					&:nth-child(#{$i}) {
						
						.results-team__col-block {
							opacity: 0.4;
						}
					}
				}
			}
		}
	}

	&__col {
		border: 1px solid $gray-lightest;
		vertical-align: middle;
		text-align: center;

		&:first-child {
			border: 1px solid $gray-169;
		}

		&_content {
			background-color: white;
		}

		&_hover {
			background-color: $blue;
			border-color: $blue;
			color: white;

			.results-team__link {
				transition: none;
				color: white;
			}
		}

		&_line {
			background-color: rgba(3, 92, 148, 0.3);
			border-color: $blue-lightest;
		}
	}

	&__col-block {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 2px;
		height: 100%;
	}

	&__link {
		color: black;
	}

	&__icon {
		display: block;
		max-width: 90%;
		width: 100%;
	}
}