.about-intro {
	position: relative;
	
	&__img {
		display: block;
		width: 100%;
	}

	&__text {
		background-color: rgba(0,92,148,0.8);
		color: white;
		font-weight: 300;
		line-height: 1.3;
		padding: 15px $el-side-padding-pc;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
	}

	&__title {
		margin-bottom: 1em;
	}
}