.intro-slider {
	font-weight: 200;
	font-size: 15px;
	position: relative;
	
	&__slide {
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;

		&_m {
			height: 430px;
		}

		&_xl {
			background-size: auto 100%;
			background-position: left top;
			height: 405px;

			&:before {
				content: "";
				background-image: linear-gradient(to left, rgba(45,45,45,1) 70%, rgba(45,45,45,0));
				position: absolute;
				top: 0;
				right: 0;
				height: 100%;
				width: 50%;
				z-index: 1;
			}
		}
	}

	&__descr {
		background-color: rgba(0,92,148,0.8);
		color: white;
		position: absolute;
		z-index: 1;

		&_m {
			left: 0;
			bottom: 0;
			width: 100%;
		}

		&_xl {
			top: 0;
			height: 100%;
			right: 0;
			width: 57%;
		}
	}
	
	&__title {
		background-color: $blue;
		font-size: 14px;
		text-transform: uppercase;
		padding-top: 9px;
		padding-right: $el-side-padding-pc;
		padding-bottom: 7px;

		&_m {
			padding-left: $el-side-padding-pc;
		}

		&_xl {
			padding-left: calc(#{$el-side-padding-pc} + 15px);
		}
	}

	&__info {
		padding-right: $el-side-padding-pc;
		padding-bottom: 8px;

		&_m {
			padding-top: 4px;
			padding-left: $el-side-padding-pc;
		}

		&_xl {
			padding-top: 15px;
			padding-left: calc(#{$el-side-padding-pc} + 15px);
		}
	}

	&__date {
		font-size: 12px;
	}

	&__text {
		margin-bottom: 0.5em;
	}

	&__actions {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 $el-side-padding-pc 7px;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		z-index: 1;
	}

	&__btn {
		background-image: url($img + "icon-intro-arrow-right.png");
		background-size: 16px 10px;
		background-repeat: no-repeat;
		background-position: left center;
		padding-left: 20px;
	}

	&__pagination {
		position: absolute;
		left: auto !important;
		right: 20px;
		bottom: 4px !important;
		z-index: 2;
		width: auto !important;

		.swiper-pagination-bullet {
			background-color: transparent;
			border: 1px solid white;
			border-radius: 0;
			opacity: 1;
			margin-left: 4px;
			height: 7px;
			width: 7px;
		}

		.swiper-pagination-bullet-active {
			background-color: white;
		}
	}
}