.team-tooltip {
	transition: $trans;
	background-color: white;
	font-size: 14px;
	opacity: 0;
	visibility: hidden;
	padding: 8px;
	position: absolute;
	white-space: nowrap;
	z-index: 1;

	&:before {
		content: "";
		border-style: solid;
		position: absolute;
		width: 0;
		height: 0;
	}
	
	&_arrow-left {

		&:before {
			transform: translateY(-50%);
			border-width: calc(#{$team-tooltip-arrow-size-pc} / 2) $team-tooltip-arrow-size-pc calc(#{$team-tooltip-arrow-size-pc} / 2) 0;
			border-color: transparent white transparent transparent;
			top: 50%;
			right: 100%;
		}
	}

	&_arrow-bottom {

		&:before {
			border-width: $team-tooltip-arrow-size-pc calc(#{$team-tooltip-arrow-size-pc} / 2) 0 calc(#{$team-tooltip-arrow-size-pc} / 2);
			border-color: white transparent transparent transparent;
			top: 100%;
			left: 19px;
		}
	}

	&__container {
		display: flex;
		align-items: center;
	}

	&__img {
		display: inline-block;
		border: 1px solid $gray;
		margin-right: 10px;
		height: 30px;
		width: 30px;
	}

	&__img-icon {
		width: 100%;
	}
}