.header {
	color: white;
	margin-bottom: 4px;

	&__block {
		
		&_nav {
			background-color: $blue;
		}
	}

	&__container {
		
		&_main {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-top: 20px;
			padding-bottom: 14px;
		}
	}

	&__item {
		
		&_logo {
			flex-shrink: 0;
		}

		&_info {
			display: flex;
			justify-content: flex-end;
			align-items: center;
			flex-shrink: 1;
			flex-grow: 1;
		}
	}

	&__info {
		font-size: 15px;
		line-height: 1.4;
		display: flex;

		p {
			margin-bottom: 0;
		}
	}

	&__info-item {
		padding-right: 40px;
	}

	&__nav {
		display: flex;
		font-size: 14px;
		font-weight: 300;
		text-transform: uppercase;
		text-align: center;
	}

	&__nav-item {
		border-right: 1px solid $blue-dark;
		flex-grow: 1;

		&:first-child {
			border-left: 1px solid $blue-dark;
		}
	}

	&__nav-link {
		display: inline-block;
		padding: 11px 4px 9px;
		text-decoration: none;
		width: 100%;

		&:hover {
			color: white;
			background-color: $blue-dark;
		}
	}
}