.statistic-options {

	&__head {

		

		&:first-child {

			.statistic-options__head-container {
				padding-bottom: 8px;
			}
		}

		&:last-child {

			.statistic-options__head-container {
				padding-bottom: 45px;
			}
		}

	}
	
	&__head-container {
		align-items: flex-start;
		padding-left: 0;
		padding-right: 0;
	}

	&__checkboxes.statistic-options__checkboxes {
		display: flex;
		align-items: flex-start;
		width: 100%;
	}

	&__item {

		&_games {
			flex-basis: 34%;
			flex-shrink: 0;
		}
		
		&_teams {
			flex-grow: 1;
			flex-shrink: 1;
			padding-right: 11%;
		}
	}

	&__teams {
		columns: 2;
	}

	&__select-all {

		.checkbox__title {
			font-weight: bold;
		}
	}
}