$agency-team-icon-size-pc: 34px;

.agency {
	background-color: $gray-superdark;
	padding-top: 30px;
	padding-bottom: 10px;

	&__list {
		display: flex;
		flex-flow: wrap;
	}

	&__item {
		display: flex;
		text-decoration: none;
		margin-bottom: 22px;
		padding: 20px;
		width: calc(100% / 3);

		&:hover {
			background-color: $gray-darken;
		}
	}

	&__block {
		display: block;

		&_photo {
			flex-shrink: 0;
		}

		&_info {
			display: flex;
			flex-flow: column;
			justify-content: space-between;
			flex-grow: 1;
			flex-shrink: 1;
			padding-left: 16px;
		}
	}

	&__photo {
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
		border: 1px solid $gray-169;
		display: block;
		position: relative;
		height: 146px;
		width: 98px;
	}

	&__team-icon {
		background-color: white;
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
		display: block;
		position: absolute;
		right: 0;
		bottom: 0;
		height: $agency-team-icon-size-pc;
		width: $agency-team-icon-size-pc;
	}

	&__info-item {
		display: block;

		&_body {
			line-height: 1.4;
		}
	}

	&__info-name {
		display: block;

		&_last {
			display: flex;
			align-items: center;
			font-size: 21px;
			line-height: 1;
			position: relative;
		}
	}

	&__info-lastname {
		padding-right: 8px;
	}

	&__info-flag {
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
		display: block;
		height: 15px;
		width: 23px;
	}

	&__info-option {
		display: block;
		font-weight: 300;
		font-size: 15px;
	}
}