$pagination-arrow-size-pc: 10px;

.pagination {
	display: inline-block;

	&__item {

		&:first-child,
		&:last-child {
			margin: 0 4px;

			.pagination__link {

				&:before {
					content: "";
					transform: rotate(45deg) translateY(-1px);
					display: inline-block;
					border-width: 2px;
					border-color: white;
					height: $pagination-arrow-size-pc;
					width: $pagination-arrow-size-pc;
				}
			}
		}

		&:first-child {

			.pagination__link {

				&:before {
					border-left-style: solid;
					border-bottom-style: solid;
				}
			}
		}

		&:last-child {

			.pagination__link {

				&:before {
					border-top-style: solid;
					border-right-style: solid;
				}
			}
		}

	}

	&__link {
		display: inline-block;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}

	&__item {
		display: inline-block;
		margin: 0 2px;
	}
}