.breadcrumbs {
	font-size: 13px;
	font-weight: 200;
	margin-bottom: 6px;
	
	&__link {
		display: inline-block;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}

		&:not(:first-child) {
			background-image: url($img + "breadcrumbs-arrow.png");
			background-position: left center;
			background-repeat: no-repeat;
			background-size: 12px 7px;
			margin-left: 4px;
			padding-left: 14px;
		}
	}
}