.news-full {
	color: white;

	&__bg {
		position: relative;

		&:before {
			content: "";
			background-image: linear-gradient(to top, black, rgba(45,45,45,0));
			position: absolute;
			bottom: 0;
			left: 0;
			height: 150px;
			width: 100%;
			z-index: 1;
		}
	}
	
	&__img {
		display: block;
		width: 100%;
	}

	&__content {
		font-weight: 300;
		position: relative;
		z-index: 1;
	}

	&__head {
		background-color: $blue;
		font-size: 14px;
		text-transform: uppercase;
		margin-top: -150px;
		padding: 8px $el-side-padding-pc 4px;
	}

	&__body {
		background-color: rgba(0,92,148,0.8);
		font-size: 15px;
		padding: 20px $el-side-padding-pc 30px;
		position: relative;
		z-index: 1;
	}

	&__date {
		font-size: 13px;
	}

	&__intro {
		margin-bottom: 40px;
	}

	&__title {
		text-transform: uppercase;
		font-size: 14px;
		margin-bottom: 1.2em;
	}

	&__results-title {
		margin-bottom: 0;
	}

	&__results-blocks {
		display: flex;
		flex-flow: wrap;
	}

	&__results-block {
		margin-bottom: 40px;
		width: 50%;
	}

	&__results-common {
		margin-bottom: 20px;
	}
}