$statistic-table-group-arrow-size-pc: 8px;

.statistic-table {

	&__wrap {

		&_head {

			.statistic-table__row-container {
				background-color: $gray-superdark;
				color: white;
				text-transform: uppercase;
	
				.statistic-table__col {
					padding-top: 14px;
					padding-bottom: 8px;
				}
			}

			
		}

		&_title {

			.statistic-table__row-container {
				background-color: $gray;
				color: white;

				.statistic-table__group-item {
					padding-top: 3px;
					padding-bottom: 1px;
				}
			}

			.statistic-table__col {

				&:nth-child(3) {

					.statistic-table__group-item {
						flex-grow: 1;
					}
				}
				
				&:nth-child(4),
				&:nth-child(5),
				&:nth-child(6),
				&:nth-child(7) {

					.statistic-table__group-item {
						flex-basis: calc(100% / 3);
					}
				}

				&:nth-last-child(2) {
					
					.statistic-table__group-item {
						flex-basis: calc(100% / 2);
					}
				}

				&:last-child {

					.statistic-table__group-item {
						flex-basis: calc(100% / 4);
					}
				}
			}

			.statistic-table__group-item {
				cursor: pointer;

				&:hover {
					background-color: $gray-dark;
				}
			}
		}
		
		&_body {

			.statistic-table__row-wrap {

				&:first-child {
					
					.statistic-table__group-item {
						padding-top: 13px;
						padding-bottom: 5px;
					}
				}
				
				&:nth-child(even) {
				
					.statistic-table__row {
						background-color: white;
					}
				}

				&:hover {

					.statistic-table__row-container {
						background-color: $blue;
						color: white;

						.statistic-table__row {
							background-color: transparent;
						}

						.statistic-table__group-item_active {
							background-color: $blue;
						}

						.statistic-table__link {
							color: white;
							text-decoration: none;
						}
					}
				}

				&_active {

					.statistic-table__row-container {
						background-color: $blue;
						color: white;

						.statistic-table__row {
							background-color: transparent;
						}

						.statistic-table__group-item_active {
							background-color: $blue;
						}

						.statistic-table__link {
							color: white;
							text-decoration: none;
						}
					}
				}
			}

			.statistic-table__col {

				&:first-child,
				&:nth-child(2) {
					font-size: 15px;
				}

				&:nth-child(3) {
					
					.statistic-table__group-item {
						flex-grow: 1;
					}
				}

				&:nth-child(4),
				&:nth-child(5),
				&:nth-child(6),
				&:nth-child(7) {

					.statistic-table__group-item {
						flex-basis: calc(100% / 3);
					}
				}

				&:nth-child(4),
				&:nth-child(6),
				&:nth-child(8) {
					background-color: $gray-opacity-6;
				}

				&:nth-last-child(2) {

					.statistic-table__group-item {
						flex-basis: calc(100% / 2);
					}
				}

				&:last-child {

					.statistic-table__group {
						justify-content: space-between;
					}

					.statistic-table__group-item {
						flex-basis: calc(100% / 4);
					}
				}
			}

			.statistic-table__group-item {
				padding-top: 13px;
				padding-bottom: 4px;
			}
		}
	}
	
	&__row-wrap {
		font-size: 12px;
		font-weight: 300;
	}

	&__row-container {
		background-color: $gray-superlight;
	}

	&__row {
		display: flex;
		padding-left: $el-side-padding-pc;
		padding-right: $el-side-padding-pc;
	}

	&__col {

		&_active {
			background-color: $gray-dark;
		}

		&:first-child {
			flex-basis: 4%;
		}

		&:nth-child(2) {
			flex-basis: 20%;
		}

		&:nth-child(3) {
			flex-basis: 4.5%;
		}

		&:nth-child(4) {
			flex-basis: 12%;
		}

		&:nth-child(5) {
			flex-basis: 12.5%;
		}

		&:nth-child(6) {
			flex-basis: 12.5%;
		}

		&:nth-child(7) {
			flex-basis: 12.5%;
		}

		&:nth-child(8) {
			flex-basis: 8%;
		}

		&:nth-child(n + 2) {
			text-align: center;
		}

		&:nth-child(n + 3) {

			.statistic-table__group {
				justify-content: space-around;
			}
		}

		&:last-child {
			flex-grow: 1;
		}
	}

	&__group {
		display: flex;
		height: 100%;
	}

	&__group-item {
		position: relative;

		&_active {
			background-color: $gray-dark;
			color: white;

			.statistic-table__group-arrow {
				display: block;
			}
		}

		&_max-min {

			
		}

		&_min-max {

			.statistic-table__group-arrow {
				transform: translateX(-50%) scaleY(-1);
			}
		}
	}

	&__group-arrow {
		transform: translateX(-50%);
		display: none;
		position: absolute;
		left: 50%;
		bottom: 100%;
		height: 10px;
		width: 20px;

		&:before {
			content: "";
			transform: rotate(45deg) translateX(-50%);
			border-top: 1px solid white;
			border-left: 1px solid white;
			position: absolute;
			top: 60%;
			left: 50%;
			height: $statistic-table-group-arrow-size-pc;
			width: $statistic-table-group-arrow-size-pc;
		}
	}

	&__link {
		color: black;
		transition: none;
	}

	&__player {
		transition: $trans;
		overflow: hidden;
		height: 0;
	}

	&__player-container {
		padding-top: 20px;
		padding-bottom: 20px;
	}
}