.teams-table {
	background-color: $gray-superdark;

	&__list {
		display: flex;
		flex-flow: wrap;
		padding: 30px 10px 5px;
	}

	&__item {
		margin-bottom: 35px;
		padding: 0 10px;
		width: 25%;
	}

	&__card {
		transition: $trans;
		display: flex;
		flex-flow: column;
		text-decoration: none;
		height: 100%;

		&:hover {
			color: $gray-superdark;

			.teams-table__card-item_bottom {

				&:before {
					height: 100%;
				}
			}

			.teams-table__card-text {
				color: $gray-superdark;
			}
		}
	}

	&__card-item {
		display: block;
		text-align: center;
		padding-left: 10px;
		padding-right: 10px;
		
		&_top {
			background-color: white;
			flex-shrink: 0;
			padding-top: 22px;
			padding-bottom: 22px;
			height: 138px;
		}

		&_bottom {
			flex-shrink: 1;
			flex-grow: 1;
			padding-top: 15px;
			padding-bottom: 10px;
			position: relative;
			z-index: 1;

			&:before {
				content: "";
				transition: $trans;
				background-color: white;
				position: absolute;
				top: 0;
				left: 0;
				height: 0;
				width: 100%;
				z-index: -1;
			}
		}
	}

	&__card-icon {
		height: 100%;
	}

	&__card-title {
		
		display: block;
	}

	&__card-text {
		transition: $trans;
		color: $gray-147;
		display: block;
		font-size: 14px;
		font-weight: 300;
		line-height: 1.3;
		margin-bottom: 0.5em;

		p {
			margin-bottom: 0;
		}
	}

	&__card-link {
		display: block;
		font-weight: 300;
		font-size: 14px;
		text-transform: uppercase;
		text-decoration: underline;
	}
}