.page-items {
	color: white;
	font-size: 12px;
	
	&_right {
		transform: translateY(-50%);
		position: absolute;
		top: 50%;
		right: $side-padding-pc;
	}

	&__title {
		margin-right: 10px;
	}

	&__list {
		display: inline-block;
	}
	
	&__item {
		display: inline-block;
		margin: 0 7px;

		&_active {

			.page-items__link {
				text-decoration: underline;
			}	
		}
	}

	&__link {
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}
}