.player-card {
	background-color: $gray-lightest;
	display: flex;
	padding: 20px 20px 30px;

	&__item {
		
		&_text {
			padding-left: 14px;
			display: flex;
			flex-flow: column;
			justify-content: space-between;
		}
	}

	&__img {
		height: 240px;
		width: 160px;
	}

	&__name {
		font-size: 21px;
		font-weight: bold;
		line-height: 1.2;
	}

	&__name-second {
		display: block;
	}

	&__info {
		font-size: 16px;
		line-height: 1.3;
	}
}